/* ----------------------------- */
/* ==variables                   */
/* ----------------------------- */
/* --------------------------------- */
/* ==global mixins                   */
/* --------------------------------- */
.mixin-hidden-input {
  position: absolute;
  top: -100vh;
  left: -100vw;
  opacity: 0;
  z-index: -100;
}
.mixin-pill-default {
  display: inline-block;
  padding: 0.25em 1em;
  background-color: var(--pill-bg-color, #D1E5EC);
  border-radius: 3px;
  color: var(--pill-text-color, #204C5A);
  cursor: pointer;
}
.mixin-pill-default:hover {
  --pill-bg-color: #A3CBD8;
}
.mixin-pill-selected {
  --pill-bg-color: #e4bc4c;
  --pill-text-color: #1e1705;
  cursor: default;
}
